/**********************Kiosk**************************/
 .wrap44 {
  height: 100vh;
}
.back10 {
  height: 100%;
}
.back10__slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: #1c1c1c;
  transition: opacity 300ms 800ms linear;
}
.back10__slide .progress {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.25rem;
  opacity: 0.5;
  background: linear-gradient(90deg, #7b29ff, #304ffe);
  transform-origin: 0% 50%;
  transform: scaleX(0);
}
.back10__slide:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
}
.back10__slide.active {
  opacity: 1;
}
.back10__slide:nth-child(even) {
  background: #fff;
}
.back10__slide:nth-child(even) .progress {
  background: #000;
  transform-origin: 100% 50%;
  opacity: 0.5;
}
.back10__slide:nth-child(1):before {
  background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/908370/jelly-back-back.png');
  opacity: 0.1;
}
.back10__slide:nth-child(2):before {
  background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/908370/mountain-back-crop2.png');
  opacity: 0.2;
  background-repeat: no-repeat;
  background-size: 101% auto;
  background-position: right calc(50% - 14rem);
}
.back10__slide:nth-child(2) .back10-front-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/908370/mountain-front-crop.png');
  background-size: 101% auto;
  background-position: center calc(50% + 21rem);
  background-repeat: no-repeat;
  z-index: 0;
  opacity: 0;
  transform: translate3d(0, 10rem, 0);
  transition: transform 600ms 300ms linear, opacity 300ms 500ms linear;
}
.back10__slide:nth-child(2) .back10-front-image:after {
  content: '';
  background: linear-gradient(rgba(255, 255, 255, 0), #fff 80%);
  display: block;
  position: absolute;
  left: 0;
  top: calc(50% + 20rem);
  height: 20.5rem;
  width: 100%;
}
.back10__slide:nth-child(2).active1 .back10-front-image {
  opacity: 0.2;
  transform: translate3d(0, 0, 0);
  transition: transform 1200ms 1300ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 1300ms linear;
}
.card99 {
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 90rem;
  height: 50rem;
  background: #000;
}
.card19__slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0rem 2rem rgba(0, 0, 0, 0);
  transition: box-shadow 400ms 400ms linear;
}
.card19__slide .number {
  opacity: 0;
  transform: translatex(-5rem);
  transition: transform 600ms 400ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 400ms linear;
}
.card19__slide .date99 {
  opacity: 0;
  transform: translatex(-5rem) rotate(-90deg);
  transition: transform 600ms 400ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 400ms linear;
}
.card19__slide.active1 {
  box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.4);
}
.card19__slide.active1 .number {
  opacity: 1;
  transform: translatex(0rem);
  transition: transform 600ms 900ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 900ms linear;
}
.card19__slide.active1 .date99 {
  opacity: 1;
  transform: translatex(0rem) rotate(-90deg);
  transition: transform 600ms 900ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 900ms linear;
}
.card19__slide.exit .number {
  transform: translatex(0rem);
  transition: transform 600ms 1000ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 200ms 1000ms linear;
}
.card19__slide.exit .date99 {
  transform: translatex(5rem) rotate(-90deg);
}
.card19__slide:nth-child(even) {
  box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.2);
}
.card19__slide:nth-child(even) .date99, .card19__slide:nth-child(even) .number {
  color: #000;
}
.card19__slide:nth-child(even) .date99 {
  left: 5.5%;
}
.card19__slide:nth-child(even) .number {
  left: 94.5%;
}
.card19__slide:nth-child(even) .date99:before {
  border-bottom: 0.15rem solid #000;
}
.card19__slide:nth-child(even) .number:before {
  border-left: 0.15rem solid #000;
}
.card19__slide:nth-child(even):after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform: scaleX(0);
  transform-origin: 100% 50%;
  transition: transform 500ms 600ms ease-in-out;
}
.card19__slide:nth-child(even).active1 {
  box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.2);
}
.card19__slide:nth-child(even).active1 .number {
  opacity: 1;
  transition: opacity 200ms 400ms linear;
}
.card19__slide:nth-child(even).active1 .date99 {
  transition: transform 600ms 1200ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 1200ms linear;
}
.card19__slide:nth-child(even).active1:after {
  transform: scaleX(1);
  transform-origin: 0% 50%;
}
.card19__slide:nth-child(even).exit .date99 {
  transform: translatex(0rem) rotate(-90deg);
}
.card19__slide:nth-child(even).exit .number {
  transform: translatex(4rem);
  transition: transform 600ms 700ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 700ms linear;
}
.card19__slide:nth-child(1) .image878 {
  background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/908370/jelly.png');
  background-size: 150% auto;
  background-position: center;
  background-repeat: no-repeat;
  width: 40%;
  height: 150%;
  position: absolute;
  top: 50%;
  left: 25%;
  transform: translate3d(-55%, -40%, 0);
  z-index: 2;
  opacity: 0;
}
.card19__slide:nth-child(1).active1 .image878 {
  opacity: 1;
  transform: translate3d(-50%, -50%, 0);
  transition: transform 1200ms 1000ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 1000ms linear;
}
.card19__slide:nth-child(1).exit .image {
  transform: translate3d(-45%, -60%, 0);
  transition: transform 600ms 300ms linear, opacity 300ms 500ms linear;
}
.card19__slide:nth-child(1):after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./bk.jpg');
  background-size: 135% auto;
  background-position: center;
  opacity: 0.15;
  filter: contrast(150%);
}
.card19__slide:nth-child(2) {
  overflow: hidden;
}
.card19__slide:nth-child(2) .image878 {
  background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/908370/mountain-front-crop.png');
  background-size: 100% auto;
  background-position: 50% top;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 55%;
  left: -5vw;
  transform: translate3d(0, 20%, 0);
  z-index: 2;
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  transition: transform 600ms 300ms linear, opacity 300ms 500ms linear;
}
.card19__slide:nth-child(2) .back10-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./qline.jpg');
  background-size: 101% auto;
  background-position: center 85%;
  background-repeat: no-repeat;
  z-index: 1;
  opacity: 0;
  transform: translatey(0);
  transition: transform 600ms 300ms linear, opacity 300ms 500ms linear;
}
.card19__slide:nth-child(2) .back10-image:after {
  content: '';
  background: linear-gradient(rgba(255, 255, 255, 0), #fff 100%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  height: 50%;
  top: 50%;
}
.card19__slide:nth-child(2).active1 .image878 {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: transform 1200ms 1300ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 1300ms linear;
}
.card19__slide:nth-child(2).active1 .back10-image {
  opacity: 1;
  transform: translatey(-2rem);
  transition: transform 1200ms 1000ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms 1000ms linear;
}
.card19__slide:nth-child(2).exit .back10-image {
  transform: translatey(-4rem);
}
.number {
  color: #fff;
  z-index: 1;
  font-family: 'Roboto Mono', monospace;
  font-weight: 500;
  letter-spacing: 0.02em;
  position: absolute;
  left: 5%;
  top: calc(50% + 6rem);
}
.number:before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translatex(-50%);
  bottom: 2.5rem;
  display: block;
  height: 4rem;
  border-left: 0.15rem solid #fff;
}
.date99 {
  color: #fff;
  z-index: 1;
  font-family: 'Roboto Mono', monospace;
  font-weight: 500;
  letter-spacing: 0.15em;
  position: absolute;
  left: 95%;
  width: 30%;
  top: calc(83% - 0.5rem);
  transform-origin: 0 50%;
  transform: rotate(-90deg);
}
.date99__time {
  opacity: 0.25;
}
.date99:before {
  content: '';
  display: inline-block;
  transform: translatey(-0.35rem);
  width: 4rem;
  margin-right: 1.5rem;
  border-bottom: 0.15rem solid #fff;
}
.content52 {
  position: absolute;
  left: 40%;
  top: 50%;
  transform: translatey(-50%);
  width: 44%;
  height: 68%;
  z-index: 2;
}
.content52__ping {
  position: absolute;
  bottom: 0;
  width: 4rem;
  transition: transform 500ms ease-out;
}
.content_52_ping:after {
  content: '';
  display: block;
  height: 0.15rem;
  background: #fff;
  width: 100%;
  transition: background 200ms 700ms linear;
  animation: widenFromLeft 1200ms ease-out;
}
@keyframes widenFromLeft {
  0% {
    transform: scaleX(1);
    transform-origin: 0 50%;
  }
  20% {
    transform: scaleX(4);
    transform-origin: 0 50%;
  }
  100% {
    transform: scaleX(1);
    transform-origin: 0 50%;
  }
}
@keyframes widenFromRight {
  0% {
    transform: scaleX(1);
    transform-origin: 100% 50%;
  }
  20% {
    transform: scaleX(4);
    transform-origin: 100% 50%;
  }
  100% {
    transform: scaleX(1);
    transform-origin: 100% 50%;
  }
}
.content52__ping--right {
  transform: translateX(35.5rem);
}
.content52__ping--right:after {
  background: #000;
  animation: widenFromRight 1200ms ease-out;
}
.content52__ping--noanimation:after {
  animation: none !important;
}
.content52__slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.content52__slide.active1 {
  pointer-events: auto;
}
.content52__slide.active1 .title9__line:before {
  transform: scaleX(1);
  transition: transform 1000ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition-delay: 1000ms;
}
.content52__slide.active1 .title9__line:nth-child(2):before {
  transition-delay: 1200ms;
}
.content52__slide.active1 .title9__inner, .content52__slide.active1 .desc12, .content52__slide.active .toShift {
  opacity: 1;
  transform: translatey(0);
}
.content52__slide.active1 .title9__inner {
  transition-delay: 1600ms;
}
.content52__slide.active1 .desc12 {
  transition-delay: 1800ms;
}
.content52__slide.active1 .toShift {
  transition-delay: 2000ms;
}
.content52__slide.exit .title9__inner, .content52__slide.exit .desc12, .content52__slide.exit .toShift{
  transform: translatey(-2.5rem);
  transition: transform 400ms ease-out, opacity 300ms linear;
}
.content52__slide.exit .title9__inner {
  transition-delay: 150ms;
}
.content52__slide.exit .desc12 {
  transition-delay: 300ms;
}
.content52__slide.exit .toShift {
  transition-delay: 450ms;
}
.content52__slide:nth-child(even) {
  text-align: right;
}
.content52__slide:nth-child(even) .title9, .content52__slide:nth-child(even) .desc12 {
  color: #000;
}
.content52__slide:nth-child(even) .learnMore {
  background: transparent;
  color: #fff;
  border: 0.15rem solid #000;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
}
.content52__slide:nth-child(even) .learnMore:before, .content52__slide:nth-child(even) .learnMore:after {
  display: none;
}
.content52__slide:nth-child(even) .button24__hover {
  background: #000;
  animation: full-rest 10s forwards;
}
@keyframes full-rest {
  0% {
    transform-origin: right center;
    transform: scaleX(1);
  }
  100% {
    transform-origin: right center;
    transform: scaleX(1);
  }
}
.content52__slide:nth-child(even) .learnMore:hover .button24__hover {
  animation: empty-swipe 500ms forwards;
}
@keyframes empty-swipe {
  0% {
    transform-origin: left center;
    transform: scaleX(1);
  }
  50% {
    transform-origin: left center;
    transform: scaleX(0);
  }
  51% {
    transform-origin: right center;
    transform: scaleX(0);
  }
  100% {
    transform-origin: right center;
    transform: scaleX(1);
  }
}
.content52__slide:nth-child(even) .title9__line:before {
  left: -4rem;
  background: #f9f9f9;
  transform-origin: 0% 50%;
}
.content52__slide:nth-child(even).active1 .title9__line:before {
  transform-origin: 100% 50%;
}
.title9 {
  color: #fff;
  font-size: 5.5rem;
  font-family: 'Playfair Display', serif;
  line-height: 1;
  letter-spacing: 0.03em;
  margin: 0 0 3.6rem;
}
.title9__line {
  display: inline-block;
  position: relative;
}
.title9__line:before {
  background: linear-gradient(90deg, #7b29ff, #304ffe);
  transform-origin: 100% 50%;
  transition: transform 500ms ease-out;
  transform: scaleX(0);
  content: '';
  display: block;
  position: absolute;
  width: calc(100% + 6rem);
  top: 2rem;
  left: -2rem;
  height: 2rem;
  z-index: -1;
}
.title9__line:nth-child(2):before {
  transition-delay: 200ms;
}
.title9__inner {
  position: relative;
  display: inline-block;
}
.desc12 {
  font-size: 1.4rem;
  font-family: 'Libre Franklin', sans-serif;
  line-height: 1.84;
  margin: 0 0 3.6rem;
  color: #fff;
}
.learnMore {
  background: rgba(0, 0, 0, 0.2);
  display: inline-block;
  font-size: 1rem;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  line-height: 1;
  font-weight: 600;
  letter-spacing: 0.1em;
  padding: 1.15rem 2rem;
  cursor: pointer;
  position: relative;
  z-index: 2;
  color: #fff;
  border-top: 0.15rem solid #304ffe;
  border-bottom: 0.15rem solid #7b29ff;
  box-shadow: 0 1rem 2rem rgba(123, 41, 255, 0.2);
  transition: transform 500ms cubic-bezier(0.165, 0.84, 0.44, 1);
}
.learnMore:before, .learnMore:after, .button24__hover {
  content: '';
  position: absolute;
  top: -1%;
  height: 102%;
  width: 0.15rem;
  background: linear-gradient(#304ffe, #7b29ff);
}
.learnMore:before {
  left: 0;
}
.learnMore:after {
  right: 0;
}
.button24__hover {
  left: -1%;
  width: 102%;
  height: 102%;
  z-index: -1;
  animation: rest 10s forwards;
}
@keyframes rest {
  0% {
    transform-origin: right center;
    transform: scaleX(0);
  }
  100% {
    transform-origin: right center;
    transform: scaleX(0);
  }
}
.learnMore:hover {
  transform: translatey(-0.5rem);
}
.learnMore:hover .button24__hover {
  animation: swipe 500ms forwards;
}
@keyframes swipe {
  0% {
    transform-origin: left center;
    transform: scaleX(0);
  }
  50% {
    transform-origin: left center;
    transform: scaleX(1);
  }
  51% {
    transform-origin: right center;
    transform: scaleX(1);
  }
  100% {
    transform-origin: right center;
    transform: scaleX(0);
  }
}
.title9__inner, .desc12, .toShift {
  opacity: 0;
  transform: translatey(2.5rem);
  position: relative;
  transition: transform 800ms cubic-bezier(0.165, 0.84, 0.44, 1), opacity 300ms linear;
}


