.ARS {
  display: inline-block;
  position: fixed;
  top: auto;
  bottom: 1vh;
  color: #ccc;
  text-align: center;
  text-decoration: none;
  font-family: Verdana, sans-serif;
  font-size: 12px;
  font-weight: 700;
  -moz-transform: translateY(10vh);
